<template>
  <a-form-model
    class="basic-detail"
    ref="ruleForm"
    :model="form"
    :rules="rules"
    :label-col="labelCol"
    :wrapper-col="wrapperCol"
  >
    <!--  -->
    <a-form-model-item label="" prop="title">
      <a-input v-model="form.title" placeholder="" />
    </a-form-model-item>
    <!--  -->
    <a-form-model-item label="" prop="desc">
      <a-textarea v-model="form.desc" placeholder="" :rows="2" />
    </a-form-model-item>
    <!--  -->
    <a-form-model-item label="" prop="category">
      <a-select
        v-model="form.category"
         placeholder=""
        @change="selectCategory"
      >
        <a-select-option v-for="c in categoryList" :value="c.id" :key="c.id">{{
          c.name
        }}</a-select-option>
      </a-select>
      <a-select v-model="form.c_items" placeholder="">
        <a-select-option v-for="c in categoryItems" :value="c" :key="c">{{
          c
        }}</a-select-option>
      </a-select>
    </a-form-model-item>
    <!--  -->
    <a-form-model-item label="" prop="tags">
      <a-select
        mode="tags"
        placeholder=""
        :default-value="['']"
        v-model="form.tags"
      >
        <a-select-option value="">
          
        </a-select-option>
      </a-select>
    </a-form-model-item>
    <a-button type="primary" @click="onSubmit">  </a-button>
  </a-form-model>
</template>
<script>
import Category from '@/api/category';

export default {
  props: ['form'],
  data() {
    return {
      // offset
      labelCol: { span: 3, offset: 3 },
      wrapperCol: { span: 13 },
      categoryList: [],
      categoryItems: [],
      rules: {
        title: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        category: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        tags: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
    };
  },
  created() {
    Category.getCategory().then((res) => {
      this.categoryList = res.data.data;
    });
  },
  methods: {
    onSubmit() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          return this.$emit('nextStep', this.form);
        }
        this.$message.error('');
        return false;
      });
    },
    // select,select
    selectCategory(val) {
      this.categoryItems.splice(0, this.categoryItems.length); // 
      this.form.c_items = null; // 
      const temp = this.categoryList[val - 1];
      for (let i = 0; i < temp.c_items.length; i += 1) {
        this.categoryItems.push(temp.c_items[i]);
      }
    },
  },
};
</script>
